.nav {
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 30px;
  padding: 20px;
  transition-timing-function: ease-in;
  transition: 0.5sec;
}
.nav_logo {
  position: fixed;
  width: 80px;
  left: 20px;
  top: 10px;
  object-fit: contain;
}
.nav_black {
  background-color: #111;
}
