* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

body {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 100%;
    letter-spacing: 1px;
}

.container {
    width: 100%;
    height: 100%; /* Added height property */
    max-width: 1000px;
    margin: 2rem auto;
    padding: 0px 20px;
  
}

.comments-container {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
}

li {
    list-style-type: none;
}
.comment{
    background-color:white;
}
@media screen and (max-width: 10980px) {
    .comments-container {
        grid-template-columns: repeat(2, 1fr);
        gap: 1.5rem;
    }
}
